import React, {useEffect, useRef, useState} from "react";
import {useHistory} from "react-router";
import Box from "@material-ui/core/Box";
import Hidden from "@material-ui/core/Hidden";
import {makeStyles} from "@material-ui/core/styles";
import ArrowBack from '@material-ui/icons/ArrowBack';
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";

import ExpandedHeader from "./ExpandedHeader";
import CapabilityHeaderBG from '../../../images/capability_header_bg.png';
import {toggleExpandedCapabilityHeader} from "../../../store/actions/rootActions";
import {useDispatch} from "react-redux";

const useStyles = makeStyles(theme => ({
    header: {
        background: theme.palette.primary.main,
        borderBottomLeftRadius: 20,
        paddingTop: 57,
        boxShadow: `0px 10px 40px 13px ${theme.palette.primary.main}73`
    },
    divider: {
        width: 60,
        height: 8,
        borderRadius: 2,
        background: theme.palette.white.main,
        [theme.breakpoints.down('sm')]: {
            height: 4
        }
    },
    title: {
        marginTop: '1rem',
        position: 'relative'
    },
    acronym: {
        marginTop: 10
    },
    breadcrumbs: {
        margin: '1rem 0'
    },
    container: {
        position: 'relative',
        [theme.breakpoints.up('xs')]: {
            margin: `0 ${theme.spacing(6)}`,
            padding: `${theme.spacing(4)} 24px`
        },
        [theme.breakpoints.up('sm')]: {
            margin: `0 ${theme.spacing(14)}`,
            padding: `${theme.spacing(4)} ${theme.spacing(6)} ${theme.spacing(3)} ${theme.spacing(6)}`,
        },
        [theme.breakpoints.up('md')]: {
            margin: `0 ${theme.spacing(18)}`,
            padding: `${theme.spacing(4)} ${theme.spacing(6)} ${theme.spacing(3)} ${theme.spacing(6)}`,
        },
        [theme.breakpoints.up('lg')]: {
            margin: `0 ${theme.spacing(22)}`,
            padding: `${theme.spacing(4)} ${theme.spacing(6)} ${theme.spacing(3)} ${theme.spacing(6)}`,
        },
        [theme.breakpoints.up('xl')]: {
            maxWidth: `calc(1620px)`,
            padding: `20px 0`,
            margin: '0 auto'
        },
        '& h1,h2,h3,h4,h5,h6': {
            color: theme.palette.white.main
        }
    },
    backButton: {
        top: '50%',
        left: -30,
        position: 'absolute',
        transform: 'translate(-100%, -50%)',
        borderRadius: theme.shape.borderRadius,
        background: theme.palette.white.main,
        [theme.breakpoints.up('xs')]: {
            left: -12,
            width: 24,
            height: 24
        },
        [theme.breakpoints.up('sm')]: {
            width: 40,
            height: 40
        },
        [theme.breakpoints.up('md')]: {
            left: -21
        },
        [theme.breakpoints.up('lg')]: {
            left: -30
        },
        '& svg': {
            color: theme.palette.primary.main,
        },
        '&:hover': {
            background: theme.palette.white.main,
            '& svg': {
                color: theme.palette.black.main,
            },
        }
    },
    headerBGContainer: {
        width: '100%',
        height: 235,
        position: 'absolute',
        left: 0,
        top: 0,
        right: 0,
    },
    headerBGImage: {
        width: '100%',
        height: '100%'
    },

}));

export default ({ breadcrumbs, title, acronym }) => {
    const history = useHistory();
    const [showHeader, toggleHeader] = useState(false);
    const classes = useStyles(showHeader);
    const headerRef = useRef();
    const dispatch = useDispatch();

    const toggleCapabilityHeader = () => {
        const { height } = headerRef?.current?.getBoundingClientRect();
        if(height && window.scrollY > height - 77) {
            toggleHeader(true)
        } else {
            toggleHeader(false)
        }
    };

    useEffect(() => {
        dispatch(toggleExpandedCapabilityHeader({ expanded: showHeader }));
    }, [showHeader]);

    useEffect(() => {
        window.addEventListener('scroll', toggleCapabilityHeader);
        return () => {
            window.removeEventListener('scroll', toggleCapabilityHeader);
            dispatch(toggleExpandedCapabilityHeader({ expanded: false }));
        }
    }, []);

    return (
        <>
            <div className={classes.header} ref={headerRef}>
                <div className={classes.headerBGContainer}>
                    <img src={CapabilityHeaderBG} alt="waves" className={classes.headerBGImage}/>
                </div>
                <div className={classes.container}>
                    <Typography variant="h6" component="h6" className={classes.breadcrumbs}>{breadcrumbs}</Typography>
                    <div className={classes.divider}/>
                    <Hidden smUp>
                        <Typography variant="h5" component="h5" className={classes.title}>
                            {title} 
                            <Box display="flex" alignItems="center" justifyContent="center" className={classes.backButton} onClick={() => history.push('/')} >
                                <ArrowBack />
                            </Box>
                        </Typography>
                    </Hidden>
                    <Hidden xsDown>
                        <Typography variant="h1" component="h1" className={classes.title}>
                            {title} 
                            <IconButton className={classes.backButton}  onClick={() => history.push('/')} >
                                <ArrowBack/>
                            </IconButton>
                        </Typography>
                    </Hidden>
                    <Hidden smUp>
                        <Typography className={classes.acronym} variant="h6" component="h6">{acronym}</Typography>
                    </Hidden>
                    <Hidden xsDown>
                        <Typography className={classes.acronym} variant="h5" component="h5">{acronym}</Typography>
                    </Hidden>
                </div>
            </div>
            <ExpandedHeader
             showHeader={showHeader} 
             title={title} 
            />
        </>
    )
}