import React, {useEffect, useState} from "react";
import Sticky from "react-stickynode";
import Grid from "@material-ui/core/Grid";
import {Container} from "@material-ui/core";
import Hidden from "@material-ui/core/Hidden";
import {Events, scrollSpy} from 'react-scroll';
import {makeStyles} from "@material-ui/core/styles";

import Sidebar from './Sidebar';
import SidebarMenu from './SidebarMenu';
import Features from './Content/Features';
import Description from './Content/Description';
import KeyBenefits from './Content/KeyBenefits';
import Terminology from './Content/Terminology';
import Technologies from './Content/Technologies';
import LinksAndMaterial from './Content/LinksAndMaterial';
import KeyConsiderations from './Content/KeyConsiderations';
import MobileSidebarMenu from "./SidebarMenu/MobileSidebarMenu";

const useStyles = makeStyles(theme => ({
    container: {
        marginTop: theme.spacing(12),
        marginBottom: theme.spacing(12),
    },
    navigationWrapper: {
        position: 'sticky',
        top: 140
    }
}));

const navItems = [
    "Description",
    "Key Benefits",
    "Key Considerations",
    "Technologies",
    "Features",
    "Links & Material",
    "Terminology",
    "Contribute",
];

export default ({
  description,
  KeyBenefitsItems,
  keyConsiderationsItems,
  technologyCategoryItems,
  featuresItems,
  resourcesItems,
  termItems,
  contributorsItem,
  capabilitiesItems
}) => {
  const classes = useStyles();
  const [activeStep, setActiveStep] = useState(1);

  useEffect(() => {
    Events.scrollEvent.register("end", function (to, element) {
      setActiveStep(to);
    });
    scrollSpy.update();
    return () => {
      Events.scrollEvent.remove("end");
    };
  }, []);

  return (
    <>
      <Hidden lgUp>
        <MobileSidebarMenu
          step={activeStep}
          setActiveStep={setActiveStep}
          navItems={navItems}
        />
      </Hidden>
      <Container maxWidth={false} component="div" className={classes.container}>
        <Grid container component="div">
          <Hidden mdDown>
            <Grid item md={2} xs={2} component="div">
                <Sticky enabled={true} top={120}>
                    <div className={classes.navigationWrapper}>
                        <SidebarMenu
                          step={activeStep}
                          setActiveStep={setActiveStep}
                          navItems={navItems}
                        />
                    </div>
                </Sticky>
            </Grid>
          </Hidden>
          <Grid item lg={7} md={8} xs={12} component="div">
            <Description description={description} scrollId="1" />
            <KeyBenefits KeyBenefitsItems={KeyBenefitsItems} scrollId="2" />
            <KeyConsiderations
              keyConsiderationsItems={keyConsiderationsItems}
              scrollId="3"
            />
            <Technologies
              technologyCategoryItems={technologyCategoryItems}
              scrollId="4"
            />
            <Features featuresItems={featuresItems} scrollId="5" />
            <LinksAndMaterial resourcesItems={resourcesItems} scrollId="6" />
            <Terminology termItems={termItems} scrollId="7" />
          </Grid>
          <Grid item lg={3} md={4} xs={12} component="div">
            <Sidebar capabilitiesItems={capabilitiesItems} contributorsItem={contributorsItem} />
          </Grid>
        </Grid>
      </Container>
    </>
  );
};