import React from 'react';
import Box from "@material-ui/core/Box";
import {NavLink} from "react-router-dom";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Typography from "@material-ui/core/Typography";
import {makeStyles, withStyles} from "@material-ui/core/styles";


const useStyles = makeStyles( theme => ({
    container: {
        position: 'fixed',
        top: 77,
        left: -372,
        height: 341,
        width: 372,
        background: theme.palette.darkBlue.main,
        transition: 'all .3s ease',
        zIndex: 99999,
        paddingLeft: 40,
        paddingTop: 20,
        boxShadow: 'none',
        "&.isOpen": {
            left: 0,
            transition: 'all .3s ease',
            borderBottomRightRadius: theme.shape.borderRadius
        },
        [theme.breakpoints.down(321)]: {
            width: '100%'
        }
    },
    root: {
        '& li': {
           paddingTop: 12,
           paddingBottom: 12,
        },
        "& a": {
            textDecoration: 'none',
            '& h6': {
                color: theme.palette.white.main,
            },
            '&.is-active': {
                '& h6': {
                    color: theme.palette.secondary.main,
                    '&::before': {
                        content: "''",
                        position: 'absolute',
                        top: 0,
                        left: 16,
                        width: 32,
                        height: 4,
                        background: theme.palette.secondary.main
                    }
                }
            }
        }
    }
}));

const menuItems = [
    {
        name: 'Home',
        to: '/'
    },
    {
        name: 'About techmap',
        to: '/about'
    },
    {
        name: 'Contact',
        to: '/contact'
    },
    {
        name: 'For experts',
        to: '/experts'
    },
];

const getMenuList = (classes, closeMenu) => {
    return (
        <List className={classes.root} component="ul">
            {
                menuItems?.map( menuItem =>
                    <ListItem component="li">
                        <NavLink exact={true} activeClassName='is-active' to={menuItem.to} onClick={closeMenu}>
                            <Typography variant="h6" component="h6">{menuItem.name}</Typography>
                        </NavLink>
                    </ListItem>
                )
            }
        </List>
    )
};


const HeaderMenu = ({ isOpen, closeMenu }) => {
    const classes = useStyles();
    return (
        <Box boxShadow={4} className={`${classes.container} ${isOpen ? 'isOpen': ''}`} >
            {getMenuList(classes, closeMenu)}
        </Box>
    )
};
export default withStyles({}, { withTheme: true })(HeaderMenu);
