import React, {useCallback, useRef, useState} from 'react';
import {useSelector} from "react-redux";
import Box from "@material-ui/core/Box";
import Paper from "@material-ui/core/Paper";
import { makeStyles } from "@material-ui/core/styles";

import Menu from './Menu';
import BurgerIcon from "./BurgerIcon";
import Logo from '../../../images/logo.svg';
import {useOnClickOutside} from "../../../utils";
import {Link} from "react-router-dom";


const useStyles = makeStyles((theme) => ({
    headerWrapper: {

    },
    container: props => ({
        position: 'fixed',
        left: '0',
        top: '0',
        width: props.expandedHeader ? 236 : 372,
        height: 77,
        backgroundColor: theme.palette.darkBlue.main,
        zIndex: 100,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        overflow: 'hidden',
        borderTopLeftRadius: 0,
        borderTopRightRadius: 0,
        borderBottomLeftRadius: 0,
        transition: 'width .333s ease',
        '&.isOpen': {
            transition: 'all .6s ease',
            borderBottomRightRadius: 0
        },
        [theme.breakpoints.down('xs')]: {
            width: props.expandedHeader ? '71px' : '100%',
            justifyContent: props.expandedHeader ? 'center' : 'flex-start',
        }
    }),
    displayFlex: {
        display: 'flex',
        alignItems: 'center'
    },
    logo: {
        marginLeft: 10,
        [theme.breakpoints.down('xs')]: props => ({
            display: props.expandedHeader ? 'none' : 'block'
        })
    }
}));

export default () => {
    const headerRef = useRef();
    const { capabilityHeader } = useSelector(state => state.rootReducer);
    const classes = useStyles({ expandedHeader: capabilityHeader?.expanded, height: 20 });
    const [isOpen, toggle] = useState(false);

    const closeMenu = () => {
        toggle(false)
    };

    const headerClickOutside = useCallback(() => {
        closeMenu();
    }, [headerRef]);

    useOnClickOutside(headerRef, headerClickOutside);

    return (
        <React.Fragment>
            <div className={classes.headerWrapper} ref={headerRef}>
                <Paper className={`${classes.container} ${isOpen ? 'isOpen' : ''}`}>
                    <Box display="flex" flexDirection="row" alignItems="center">
                        <BurgerIcon isOpen={isOpen} toggle={toggle} />
                        <Link to="/">
                            <img src={Logo} className={classes.logo} alt="TechMap" height={36} />
                        </Link>
                    </Box>
                </Paper>
                <Menu isOpen={isOpen} closeMenu={closeMenu}/>
            </div>
        </React.Fragment>
    )
}