import React, {useEffect} from "react";
import Slide from "@material-ui/core/Slide";
import {useSelector} from "react-redux";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import {makeStyles} from "@material-ui/core/styles";

import WelcomeLogin from './welcomeLogin';
import Waves from '../../images/login/waves.png';


const useStyles = makeStyles( theme => ({
    dialog: {
        textAlign: 'center',
        '& .MuiDialog-paper': {
            width: 500,
            maxWidth: '100%',
        },
        '& .MuiDialogTitle-root': {
            paddingTop: theme.spacing(8),
            paddingLeft: theme.spacing(7),
            paddingRight: theme.spacing(4)
        },
        '& .MuiDialogContent-root': {
            paddingLeft: theme.spacing(7),
            paddingRight: theme.spacing(7),
            paddingBottom: theme.spacing(5)
        },
        '& .MuiDialogActions-root': {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            paddingTop: theme.spacing(4),
            paddingLeft: theme.spacing(7),
            paddingRight: theme.spacing(7),
            paddingBottom: theme.spacing(6),
            '& > button': {
                flex: 1,
                margin: `0 ${theme.spacing(3)}`,
                paddingTop: theme.spacing(4),
                paddingBottom: theme.spacing(4),
                fontSize: 18,
                fontWeight: 'bold'
            }
        },
        '& p': {
            fontSize: 18
        }
    },
    title: {
        '& span': {
            color: theme.palette.deepBlue.main
        },
        '&:before': {
            content: "''",
            display: 'block',
            backgroundColor: theme.palette.deepBlue.main,
            width: 26,
            height: 4,
            borderRadius: 1,
            margin: '0 auto',
            marginBottom: theme.spacing(2)
        }
    },
    subtitle_1: {
        textTransform: 'uppercase',
        color: theme.palette.brightBlue.main,
        marginTop: theme.spacing(6)
    },
    subtitle_2: {
        textTransform: 'uppercase',
        color: theme.palette.brightBlue.main,
        marginTop: -10,
        marginBottom: theme.spacing(6)
    },
    imageBG: {
        position: 'absolute',
        bottom: 0,
        right: 0,
        left: 0,
        width: '100%',
        height: 70,
        '& img': {
            width: '100%',
            height: '100%'
        }
    }
}));

const Transition = props => {
    return <Slide direction="right" {...props} timeout={{ enter: 500, exit: 333 }} />;
}

export default () => {
    const { loggedIn } = useSelector(state => state.authReducer);
    const [showLogin, toggleShowLogin] = React.useState(false);
    const [showWelcome, toggleShowWelcome] = React.useState(true);
    const classes = useStyles();

    useEffect(() => {
        toggleShowWelcome(!loggedIn);
    }, [loggedIn]);

    const handleClose = () => {
        toggleShowLogin(false);
    };

    const onShowLogin = () => {
        toggleShowWelcome(false);
        toggleShowLogin(true)
    };

    return (
        <>
            <Dialog
                className={classes.dialog}
                aria-labelledby="customized-dialog-title"
                open={showLogin}
                TransitionComponent={Transition}
            >
                <div className={classes.imageBG}>
                    <img src={Waves} alt=""/>
                </div>
                <div style={{ padding: 20, borderRadius: 10, textAlign:"center"}}>
                    <p>Login Modal Window</p>

                    <Button  color="secondary" variant="contained" onClick={() => handleClose()}>Login</Button>
                </div>
            </Dialog>
            <Dialog
                className={classes.dialog}
                aria-labelledby="customized-dialog-title"
                open={showWelcome}
                TransitionComponent={Transition}
            >
                <div className={classes.imageBG}>
                    <img src={Waves} alt=""/>
                </div>
                <WelcomeLogin classes={classes} onShowLogin={onShowLogin}/>
            </Dialog>
        </>

    )
}