import React from "react";
import Paper from "@material-ui/core/Paper/Paper";
import {makeStyles} from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";

import CapabilityBG from "../../../images/capability_bg.png";
import Capabilities from "../Capabilities";

const useStyles = makeStyles( theme => ({
    capabilityContainer: {
        padding: `0 ${theme.spacing(6)}`,
        marginTop: theme.spacing(6),
        zIndex: 3
    }
}));

export default ({ capability, classes, zoomState, transformOptions, categoryOptions }) => {
    const innerClasses = useStyles();

    return (
        <div className={innerClasses.capabilityContainer}>
            <Paper className={classes.capability} elevation={4}>
                <div className={classes.capabilityBGContainer}>
                    <img src={CapabilityBG} alt="waves" className={classes.techBGImage}/>
                </div>
                <div className={classes.upperline}/>
                <Typography variant="h5" component="h5" className={classes.capabilityTitle}>
                    {capability['Category Name']}
                </Typography>
                {
                    zoomState < 2 ?
                        <Capabilities classes={classes} capability={capability} transformOptions={transformOptions} categoryOptions={categoryOptions} />
                        :
                        null
                }

            </Paper>
        </div>
    )
}
