import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import ScrollElement from "./ScrollElement";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles( theme => ({
    scrollElement: {
        paddingBottom: theme.spacing(6),
        borderBottom: `2px solid ${theme.palette.darkBlue.main}33`,
        marginBottom: theme.spacing(12)
    },
    header: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        marginBottom: theme.spacing(4),
        [theme.breakpoints.down('sm')]: {
            alignItems: 'flex-start',
            flexDirection: 'column',
        }
    },
    content: {

    },
    leftSide: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'flex-start',
    },
    headerIcon: {
        color: theme.palette.primary.main,
        display: 'flex',
        alignItems: 'center'
    },
    title: {
        marginLeft: 10
    },
    headerRightSideContent: {
        [theme.breakpoints.down('sm')]: {
            marginTop: theme.spacing(2)
        }
    }
}));

export default ({ children, title, icon, scrollId, headerRightSideContent = null }) => {
    const classes = useStyles();
    return (
        <ScrollElement name={scrollId} className={classes.scrollElement} data-step={scrollId}>
            <div className={classes.sectionContainer}>
                <div className={classes.header}>
                    <div className={classes.leftSide}>
                        {
                            icon ?
                                <span className={classes.headerIcon}>{icon}</span>
                                :
                                null
                        }
                        <Typography variant="h5" component="h5" className={classes.title}>{title}</Typography>
                    </div>
                    {
                        headerRightSideContent ?
                            <div className={classes.headerRightSideContent}>
                                {headerRightSideContent}
                            </div>
                            :
                            null
                    }
                </div>
                <div className={classes.content}>{children}</div>
            </div>
        </ScrollElement>
    )
}