import {
    TECHMAP_FOCUS_CATEGORY,
    TECHMAP_FUNCTIONAL_CATEGORY_FAILURE,
    TECHMAP_FUNCTIONAL_CATEGORY_REQUESTING,
    TECHMAP_FUNCTIONAL_CATEGORY_SUCCESS, TECHMAP_POSITION, TECHMAP_SCALE,
    TECHMAP_USE_MOUSE,
    TECHMAP_USE_TOUCH,
    TECHMAP_ZOOM_IN,
    TECHMAP_ZOOM_OUT,
    FETCH_SINGLE_CAPABILITY,
} from "../types";
import axios from "axios";

import {BE_URL, SUCCESS} from "../../constants";

/* Fetch Functional Category */
const fetchTechMapFunctionalCategory = () => async dispatch => {
    dispatch({type: TECHMAP_FUNCTIONAL_CATEGORY_REQUESTING});
    try {
        const { data } = await axios.get(`${BE_URL}/tech-map`);
        dispatch({type: TECHMAP_FUNCTIONAL_CATEGORY_SUCCESS, payload: data});
    } catch (e) {
        dispatch({type: TECHMAP_FUNCTIONAL_CATEGORY_FAILURE, error: e.message});
    }
};

export const fetchSingleCapability = (id) => async dispatch => {
    try {
        const { data } = await axios.get(`${BE_URL}/capability?id=${id}`);
        dispatch({type: FETCH_SINGLE_CAPABILITY, payload: data});
    } catch (e) {
        console.log(e);
    }
}

export const fetchTechMapFunctionalCategoryIfNeeded = () => (dispatch, getState) => {
    if (
        getState()?.techMapReducer?.techMapData?.readyStatus !== SUCCESS
    ) {
        dispatch(fetchTechMapFunctionalCategory());
    }
};

/* TechMap SpeedDial */
export const techMapZoomOutAction = zoomState => ({ type: TECHMAP_ZOOM_IN, zoomState });
export const techMapZoomInAction = zoomState => ({ type: TECHMAP_ZOOM_OUT, zoomState });
export const techMapScaleAction = scale => ({ type: TECHMAP_SCALE, scale });
export const useMouseAction = () => ({ type: TECHMAP_USE_MOUSE });
export const useTouchAction = () => ({ type: TECHMAP_USE_TOUCH });

export const setTechMapPosition = position => ({ type: TECHMAP_POSITION, position });

export const setTechMapFocusedCategory = categoryId => ({ type: TECHMAP_FOCUS_CATEGORY, categoryId });