import {produce} from 'immer';

import {
    TECHMAP_SCALE,
    TECHMAP_ZOOM_IN,
    TECHMAP_ZOOM_OUT,
    TECHMAP_POSITION,
    TECHMAP_USE_MOUSE,
    TECHMAP_USE_TOUCH,
    FETCH_SINGLE_CAPABILITY,
    TECHMAP_FOCUS_CATEGORY,
    TECHMAP_FUNCTIONAL_CATEGORY_REQUESTING,
    TECHMAP_FUNCTIONAL_CATEGORY_FAILURE,
    TECHMAP_FUNCTIONAL_CATEGORY_SUCCESS
} from "../types";

import { FAILURE, SUCCESS, REQUESTING } from '../../constants';

const MAX_ZOOM = 3;
const MIN_ZOOM = 1;

const initialState = {
    techMapOptions: {
        zoomState: 1,
        useTouch: true,
        useMouse: false,
        position: {
            x: 100,
            y: 100
        },
        scale: .5,
        focusedCategory: null
    },
    techMapData: {
        readyStatus: "invalid",
        err: null,
        data: []
    }
};

export default produce((state = initialState, action) => {
    switch (action.type) {
        case TECHMAP_ZOOM_IN:
            if(action.zoomState)
                state.techMapOptions.zoomState = action.zoomState;
            else {
                state.techMapOptions.zoomState -= state.techMapOptions.zoomState > MIN_ZOOM && 1;
            }
            break;
        case TECHMAP_ZOOM_OUT:
            if(action.zoomState)
                state.techMapOptions.zoomState = action.zoomState;
            else
                state.techMapOptions.zoomState += state.techMapOptions.zoomState < MAX_ZOOM && 1;
            break;
        case TECHMAP_SCALE:
            state.techMapOptions.scale = action?.scale;
            break;
        case TECHMAP_USE_MOUSE:
            state.techMapOptions.useMouse = true;
            state.techMapOptions.useTouch = false;
            break;
        case TECHMAP_USE_TOUCH:
            state.techMapOptions.useMouse = false;
            state.techMapOptions.useTouch = true;
            break;
        case TECHMAP_FUNCTIONAL_CATEGORY_REQUESTING:
            state.techMapData.readyStatus = REQUESTING;
            break;
        case TECHMAP_FUNCTIONAL_CATEGORY_FAILURE:
            state.techMapData.readyStatus = FAILURE;
            state.techMapData.err = action.error;
            break;
        case TECHMAP_FUNCTIONAL_CATEGORY_SUCCESS:
            state.techMapData.readyStatus = SUCCESS;
            state.techMapData.data = action.payload;
            break;
        case TECHMAP_POSITION:
            if(action.position){
                state.techMapOptions.position = { x: action.position.x, y: action.position.y };
            }
            break;
        case FETCH_SINGLE_CAPABILITY:
            if(action.payload) {
                state.singleCapability = action.payload;
            }
            break;
        case TECHMAP_FOCUS_CATEGORY:
            state.techMapOptions.focusedCategory = action?.categoryId;
            break;
        default:
            return state;
    }
})