export default ({ scrollId, technologyCategoryItems }) => {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);
    const [expandedMoreButton, setMoreButtonExpanded] = useState(false);
    let items = technologyCategoryItems;
    let categoryFilterTags = technologyCategoryItems?.map(item => item["Technology Type"]);
    categoryFilterTags = [...new Set(categoryFilterTags)].map(item => {
        return {
            type: "tag",
            name: item
        }
    });

    const [state, setState] = useState({
        filter: ""
    });
    
    const handleFilter = (filter) => {
        setState({
            ...state,
            filter
        })
    }

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };
    if(state.filter) {
        items = items.filter(item => {
            if(state.filter === "") {
                return true;
            }
            return item["Technology Type"] === state.filter
        })
    }
    return (
        <SectionLayout icon={<Build />} title="Technologies" scrollId={scrollId}>
            <div className={classes.filters}>
                <TagsList tags={categoryFilterTags} onClickHandler={handleFilter} containerStyles={classes.filters} />
            </div>
            {
                items?.slice(0,4).map( (item, index) => {
                    const resources = item["Related Resources"];
                    return (
                        <Accordion expanded={expanded === item.id} onChange={handleChange(item.id)} key={item.id}>
                            <AccordionSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls={`${item.id}-content`}
                                id={`${item.id}-header`}
                            >
                                <Typography variant="h6" component="h6" className={classes.heading}>{item['Technology Category Name']}</Typography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <TagsList tags={[{ type:"tag", name: item["Acronym"]}]} />
                                <Typography variant="body1" component="p">
                                    {item.Description}
                                </Typography>
                                <div className={classes.linksWrapper}>
                                    <div className={classes.linkColumn}>
                                    {resources.slice(0, Math.ceil(resources.length /  2)).map(link => {
                                    return (<Link href={link["Resource Link"]} className={classes.linkItem} component="a">
                                                {link["Resource Name"]}
                                            </Link>
                                    )
                                    })}
                                    </div>
                                    <div className={classes.linkColumn}>
                                    {resources.slice(Math.ceil(resources.length /  2)).map(link => {

                                    return (<Link href={link["Resource Link"]} className={classes.linkItem} component="a">
                                                {link["Resource Name"]}
                                            </Link>
                                    )
                                    })}
                                    </div>
                                </div>
                            </AccordionDetails>
                        </Accordion>
                    )
                })
            }

            <Collapse in={expandedMoreButton} timeout="auto" unmountOnExit style={{ marginTop: '1rem'}}>
            {
                items?.slice(4).map( (item, index) => {
                    const resources = item["Related Resources"];
                    return (
                        <Accordion expanded={expanded === item.id} onChange={handleChange(item.id)} key={item.id}>
                            <AccordionSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls={`${item.id}-content`}
                                id={`${item.id}-header`}
                            >
                                <Typography variant="h6" component="h6" className={classes.heading}>{item['Technology Category Name']}</Typography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <TagsList tags={[{ type:"tag", name: item["Acronym"]}]} />
                                <Typography variant="body1" component="p">
                                    {item.Description}
                                </Typography>
                                <div className={classes.linksWrapper}>
                                    <div className={classes.linkColumn}>
                                        {resources.slice(0, Math.ceil(resources.length /  2)).map(link => {

                                            return (<Link href={link["Resource Link"]} className={classes.linkItem} component="a">
                                                        {link["Resource Name"]}
                                                    </Link>
                                            )
                                        })}
                                    </div>
                                    <div className={classes.linkColumn}>
                                    {resources.slice(Math.ceil(resources.length /  2)).map(link => {

                                    return (<Link href={link["Resource Link"]} className={classes.linkItem} component="a">
                                                {link["Resource Name"]}
                                            </Link>
                                    )
                                    })}
                                    </div>
                                </div>
                            </AccordionDetails>
                        </Accordion>
                    )
                })
            }
            </Collapse>
            <Button
                onClick={() => setMoreButtonExpanded(!expandedMoreButton)}
                endIcon={expandedMoreButton ? <Remove/> : <Add />}
                className={classes.moreButton}
            >
                {expandedMoreButton ? "Show less" : "Show more"}
            </Button>
        </SectionLayout>
    )
}