import React from "react";
import Box from "@material-ui/core/Box";
import IconButton from "@material-ui/core/IconButton";
import ArrowBack from "@material-ui/icons/ArrowBack";
import Typography from "@material-ui/core/Typography";
import {makeStyles} from "@material-ui/core/styles";
import {useHistory} from "react-router";

import CapabilityHeaderBG from '../../../images/capability_header_bg.png';
import Hidden from "@material-ui/core/Hidden";

const useStyles = makeStyles(theme => ({
    expandedHeader: showHeader => ({
        position: 'fixed',
        background: theme.palette.primary.main,
        top: showHeader ? 0 : -77,
        left: 60,
        right: 0,
        height: 77,
        transition: 'all .333s ease',
        transitionDelay: showHeader ? '.2s' : '0s',
        zIndex: 10,
        [theme.breakpoints.up('sm')]: {
            left: 224,
        }
    }),
    scrollHeaderBG: {
        width: '100%',
        height: 47,
        position: 'absolute',
        left: 0,
        top: 0,
        right: 0,
    },
    expandedHeaderBackButton : {
        borderRadius: theme.shape.borderRadius,
        background: theme.palette.white.main,
        width: 32,
        height: 32,
        '& svg': {
            color: theme.palette.primary.main,
            width: 25
        }
    },
    expandedHeaderContainer: {
        paddingLeft: 28,
        height: '100%',
        '& h5, h6': {
            color: theme.palette.white.main,
            marginLeft: 16,
            [theme.breakpoints.down('xs')]: {
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                display: '-webkit-box',
                lineClamp: 1,
                boxOrient: 'vertical'
            }
        }
    },
    headerBGImage: {
        width: '100%',
        height: '100%'
    }
}));

export default ({ title, showHeader }) => {
    const classes = useStyles(showHeader);
    const history = useHistory();

    return (
        <div className={classes.expandedHeader}>
            <div className={classes.scrollHeaderBG}>
                <img src={CapabilityHeaderBG} alt="waves" className={classes.headerBGImage}/>
            </div>
            <Box
                display="flex"
                alignItems="center"
                justifyContent="flex-start"
                className={classes.expandedHeaderContainer}
            >
                <IconButton className={classes.expandedHeaderBackButton} onClick={() => history.push('/')}>
                    <ArrowBack/>
                </IconButton>
                <Hidden smUp>
                    <Typography variant="h6">
                        {title}
                    </Typography>
                </Hidden>
                <Hidden xsDown>
                    <Typography variant="h5">
                        {title}
                    </Typography>
                </Hidden>
            </Box>
        </div>
    )
}