import React from "react";
import Masonry from "react-masonry-css";
import Paper from "@material-ui/core/Paper/Paper";
import {makeStyles, useTheme} from "@material-ui/core/styles";

import FunctionalCategoryHeader from './Header';
import CapabilityCategory from "../CapabilityCategory";
import EmptyCapabilityCategories from "../CapabilityCategory/EmptyCapabilityCategories";

const useStyles = makeStyles(theme => ({
    capability: {
        width: 448,
        padding: `${theme.spacing(8)} ${theme.spacing(8)} 24px ${theme.spacing(8)}`,
        position: 'relative',
        boxShadow: `0px 5px 20px 11px ${theme.palette.primary.main}20`,
        [theme.breakpoints.down('sm')]: {
            width: 'auto',
            maxWidth: 448
        },
    },
    groupContainer: props => ({
        width: 'auto',
        maxWidth: 'none',
        minWidth: props.activeTech || props.zoomState === 3 ? props.isFocused ? "auto" : 600 : "auto",
        '& > .MuiPaper-root': {
            background: `${props?.color}1a`
        }
    }),
    minScaleLayout: {
        margin: 0
    },
    capabilityTitle: {
        textAlign: 'left',
        marginBottom: 10
    },
    upperline: props => ({
        width: 40,
        height: 4,
        background: props?.color,
        marginBottom: 15,
        borderRadius: 1
    }),
    capabilitiesContainer: props => ({
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'nowrap',
        justifyContent: 'center',
        marginTop: props.activeTech || props.zoomState === 3 ? props.isFocused ? -77 : 0 : -77,
        position: 'relative',
        paddingBottom: props.activeTech || props.zoomState === 3 ? props.isFocused ? theme.spacing(8) : 0 : theme.spacing(8),
        // paddingBottom: props.zoomState < 3 ? theme.spacing(8) : 0,
        borderRadius: 8,
        zIndex: props.zoomState < 3 ? 3 : 2
    }),
    group: {
        display: 'flex',
        flexDirection: 'column'
    },
    technologiesContainer: {
        marginTop: 20
    },
    capabilityBGContainer: {
        width: '100%',
        height: 95,
        position: 'absolute',
        left: 0,
        bottom: 0,
        right: 0,
        '& img': {
            width: '100%',
            height: '100%'
        }
    },
    masonryGrid: {
        display: 'flex',
        width: 'auto',
        maxWidth: '100%'
    }
}));

const renderFunctionalCategoryHeaderContent = (classes, functionalCategory, zoomState, transformOptions, categoryOptions, theme) => {
    return (
        <Masonry
            breakpointCols={theme.techMapColumns.capabilityCategory}
            className={classes.masonryGrid}
            columnClassName="my-masonry-grid_column"
        >
            {
                functionalCategory?.relatedCapabilitiesCategoriesWithCapability?.map(capability => {
                    return (
                        <CapabilityCategory
                            classes={classes}
                            key={capability.id}
                            zoomState={zoomState}
                            capability={capability}
                            categoryOptions={categoryOptions}
                            transformOptions={transformOptions}
                        />
                    )
                })
            }
        </Masonry>
    )
};


export default ({ zoomState, functionalCategory, activeTech, transformOptions, fitFocusedCategory }) => {
    const theme = useTheme();
    const isFocused = functionalCategory?.id === activeTech;
    const categoryOptions = theme.functionalCategories[functionalCategory?.name.split(' ').join('').toLowerCase()];

    const classes = useStyles({ zoomState, color: categoryOptions.color, isFocused, activeTech });

    return (
        <Paper elevation={0}>
            <div className={`${classes.groupContainer} ${ zoomState > 2 ? classes.minScaleLayout : ''}`}>
                <Paper className={classes.group} elevation={0}>
                    <FunctionalCategoryHeader
                        zoomState={zoomState}
                        activeTech={activeTech}
                        categoryOptions={categoryOptions}
                        transformOptions={transformOptions}
                        functionalCategory={functionalCategory}
                        fitFocusedCategory={fitFocusedCategory}
                    />
                    {/*<Zoom in={true} timeout={330}>*/}
                        <div className={classes.capabilitiesContainer}>
                            {
                                activeTech && activeTech === functionalCategory?.id && zoomState < 3 ?
                                    functionalCategory.relatedCapabilitiesCategoriesWithCapability && functionalCategory.relatedCapabilitiesCategoriesWithCapability?.length ?
                                        renderFunctionalCategoryHeaderContent(classes, functionalCategory, zoomState, transformOptions, categoryOptions, theme)
                                        :
                                        <EmptyCapabilityCategories classes={classes} zoomState activeTech />
                                    :
                                    !activeTech && zoomState < 3 ?
                                        functionalCategory.relatedCapabilitiesCategoriesWithCapability && functionalCategory.relatedCapabilitiesCategoriesWithCapability?.length ?
                                            renderFunctionalCategoryHeaderContent(classes, functionalCategory, zoomState, transformOptions, categoryOptions, theme)
                                            :
                                            <EmptyCapabilityCategories classes={classes} zoomState activeTech />
                                        :
                                        null
                            }
                        </div>
                    {/*</Zoom>*/}
                </Paper>
            </div>
        </Paper>
    )
}