import React from "react";
import {useHistory} from "react-router";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper/Paper";
import {makeStyles} from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import {setTechMapPosition, fetchSingleCapability} from "../../../store/actions/techMapActions";
import {useDispatch} from "react-redux";

const useStyles = makeStyles( theme => ({
    description: {
        display: '-webkit-box',
        lineClamp: 8,
        boxOrient: 'vertical',
        overflow: 'hidden'
    },
    capabilityButton: props => ({
        width: '100%',
        height: 80,
        backgroundColor: props?.categoryOptions.color,
        color: theme.palette.white.main,
        '&:hover': {
            backgroundColor: theme.palette.darkBlue.main
        }
    }),
    contributeButton: {
        backgroundColor: theme.palette.darkBlue.main
    }
}));

export default  ({ classes, capability, categoryOptions, transformOptions }) => {
    const innerStyles = useStyles({ categoryOptions });
    let history = useHistory();
    const dispatch = useDispatch();

    const onCapabilityClick = (id) => {
        dispatch(setTechMapPosition({x: transformOptions.positionX, y: transformOptions.positionY}));
        dispatch(fetchSingleCapability(id));
        history.push(`/capability?id=${id}`);
    };

    return (
        <>
        {/*<Zoom in={true} timeout={330}>*/}
            <Paper elevation={0}>
                {
                    capability['Related Capabilities'] && capability['Related Capabilities'].length ?
                        <>
                            <Typography variant="body1" className={innerStyles.description}>{capability['Category Description']}</Typography>
                            <Grid container className={classes.technologiesContainer} spacing={2} component="div">
                                {
                                    capability['Related Capabilities'].map(technology =>
                                        <Grid item md={6} xs={6} key={technology?.id} component="div">
                                            <Button
                                                key={technology.id}
                                                variant="contained"
                                                color="primary"
                                                className={innerStyles.capabilityButton}
                                                onClick={() => onCapabilityClick(technology.id)}
                                            >
                                                {technology['Capability Name']}
                                            </Button>
                                        </Grid>
                                    )
                                }
                            </Grid>
                        </>
                        :
                        <>
                            <Typography component="p" variant="body1" className="title">
                                <b>We havent had the capability yet to finalise this.</b>
                            </Typography>
                            <Typography component="p" variant="body1" className="title">
                                But good news is that  you can still helps us build.
                            </Typography>
                            <Grid container className={classes.technologiesContainer} spacing={2} component="div">
                                <Button
                                    variant="contained"
                                    color="primary"
                                    className={innerStyles.contributeButton}
                                >
                                    Contribute
                                </Button>
                            </Grid>
                        </>
                }

            </Paper>
        {/*</Zoom>*/}
        </>
    )
}