import React from "react";
import {useDispatch} from "react-redux";
import Typography from "@material-ui/core/Typography";
import MouseIcon from '@material-ui/icons/Mouse';
import SpeedDial from "@material-ui/lab/SpeedDial/SpeedDial";
import SpeedDialIcon from "@material-ui/lab/SpeedDialIcon/SpeedDialIcon";
import SpeedDialAction from "@material-ui/lab/SpeedDialAction/SpeedDialAction";
import LayersIcon from '@material-ui/icons/Layers';
import TouchAppIcon from '@material-ui/icons/TouchApp';
import LayersClearIcon from '@material-ui/icons/LayersClear';
import SettingsBackupRestoreIcon from '@material-ui/icons/SettingsBackupRestore';

import {
    setTechMapFocusedCategory,
    setTechMapPosition,
    techMapScaleAction,
    techMapZoomInAction,
    techMapZoomOutAction,
    useMouseAction,
    useTouchAction
} from "../../../store/actions/techMapActions";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles( theme => ({
    speedDialWrapper: {
        position: 'absolute',
        bottom: `calc(56px + ${theme.spacing(4)})`,
        left: 20,
        zIndex: 200
    },
    speedFabDialIcon: {
        background: theme.palette.darkBlue.main,
        width: 48,
        height: 48
    },
    speedDialIcon: {
        height: '2.1rem'
    },
    speedDialLabel: {
        fontSize: '2.1rem'
    },
    speedDialActionsBG: {
        background: theme.palette.darkBlue.main,
        color: theme.palette.white.main,
        '&:hover': {
            background: theme.palette.darkBlue.main,
            color: `${theme.palette.white.main}50`
        }
    },
    speedDialActionsIcon: {
        fontSize: '1.2rem'
    },
    tooltip: {
        width: 130,
        background: 'none',
        boxShadow: 'none',
        '& p': {
            color: theme.palette.white.main,
            fontWeight: 'bold'
        }
    }
}));

export default ({ handleOpen, handleClose, zoomState, useTouch, useMouse, open }) => {
    const dispatch = useDispatch();
    const classes = useStyles();

    const backToStart = () => {
        dispatch(setTechMapPosition({ x: 100, y: 100 }));
        dispatch(techMapScaleAction(.5));
        dispatch(setTechMapFocusedCategory(null))
    };

    return (
        <div className={classes.speedDialWrapper}>
            <SpeedDial
                ariaLabel="SpeedDial example"
                icon={
                    <SpeedDialIcon classes={{
                        icon: classes.speedDialLabel,
                        root: classes.speedDialIcon
                    }}/>
                }
                onClose={handleClose}
                onOpen={handleOpen}
                open={open}
                classes={{
                    fab: classes.speedFabDialIcon
                }}
            >
                <SpeedDialAction
                    title="Use mouse"
                    classes={{
                        staticTooltipLabel: classes.tooltip,
                        fab: classes.speedDialActionsBG
                    }}
                    FabProps={{
                        disabled: useMouse
                    }}
                    component="button"
                    key={"Use mouse"}
                    icon={<MouseIcon className={ classes.speedDialActionsIcon } component="svg" />}
                    tooltipTitle={<Typography variant="body2" component="p">Use mouse</Typography>}
                    onClick={() => dispatch(useMouseAction())}
                    tooltipOpen
                    tooltipPlacement="right"
                />
                <SpeedDialAction
                    title="Use touch"
                    classes={{
                        staticTooltipLabel: classes.tooltip,
                        fab: classes.speedDialActionsBG
                    }}
                    FabProps={{
                        disabled: useTouch
                    }}
                    component="button"
                    key={"Use touch"}
                    icon={<TouchAppIcon className={ classes.speedDialActionsIcon } component="svg" />}
                    tooltipTitle={<Typography variant="body2" component="p">Use touch</Typography>}
                    onClick={() => dispatch(useTouchAction())}
                    tooltipOpen
                    tooltipPlacement="right"
                />
                <SpeedDialAction
                    title="Back to start"
                    classes={{
                        staticTooltipLabel: classes.tooltip,
                        fab: classes.speedDialActionsBG
                    }}
                    component="button"
                    key={"Back to start"}
                    icon={<SettingsBackupRestoreIcon className={ classes.speedDialActionsIcon } component="svg" />}
                    tooltipTitle={<Typography variant="body2" component="p">Back to start</Typography>}
                    onClick={backToStart}
                    tooltipOpen
                    tooltipPlacement="right"
                />
                <SpeedDialAction
                    title="Show level"
                    classes={{
                        staticTooltipLabel: classes.tooltip,
                        fab: classes.speedDialActionsBG
                    }}
                    FabProps={{
                        disabled: zoomState < 2
                    }}
                    component="button"
                    key={'Show level'}
                    icon={<LayersIcon className={ classes.speedDialActionsIcon } component="svg" />}
                    tooltipTitle={<Typography variant="body2" component="p">Show level</Typography>}
                    onClick={() => dispatch(techMapZoomOutAction())}
                    tooltipOpen
                    tooltipPlacement="right"
                />
                <SpeedDialAction
                    title="Hide level"
                    classes={{
                        staticTooltipLabel: classes.tooltip,
                        fab: classes.speedDialActionsBG
                    }}
                    FabProps={{
                        disabled: zoomState > 2
                    }}
                    component="button"
                    key={'Hide level'}
                    icon={<LayersClearIcon className={ classes.speedDialActionsIcon } component="svg" />}
                    tooltipTitle={<Typography variant="body2" component="p">Hide level</Typography>}
                    onClick={() => dispatch(techMapZoomInAction())}
                    tooltipOpen
                    tooltipPlacement="right"
                />
            </SpeedDial>
        </div>
    )
}