import React from 'react';
import {useDispatch} from "react-redux";
import Backdrop from "@material-ui/core/Backdrop";
import { makeStyles } from "@material-ui/core/styles";
import { TransformComponent, TransformWrapper } from "../../modules/dist";
// import { TransformComponent, TransformWrapper } from "react-zoom-pan-pinch";
import CircularProgress from "@material-ui/core/CircularProgress/CircularProgress";

import TechMapLayout from './TechMapLayout';
import {REQUESTING} from "../../constants";
import SpeedDialComponent from './SpeedDial';
import {setTechMapPosition, techMapScaleAction} from "../../store/actions/techMapActions";

const useStyles = makeStyles( theme => ({
    canvas: {
        width: '100%',
        height: '100vh',
        overflow: 'hidden'
    },
    backdrop: {
        zIndex: 1,
        background: `${theme.palette.darkBlue.main}b3`
    },
    loader: {
        position: 'absolute',
        left: '50%',
        top: '50%',
        transform: 'translate(-50%, -50%)'
    },
    techMapLayoutContainer: {
        height: '100vh',
        width: '100vw'
    }
}));

export default ({ zoomState, techMapData, useTouch, useMouse, techMapPosition, techMapScale, focusedCategory }) => {
    const [open, setOpen] = React.useState(false);
    const classes = useStyles();
    const dispatch = useDispatch();

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const onScaleStop = ({ scale, positionX, positionY }) => {
        dispatch(techMapScaleAction(scale));
        dispatch(setTechMapPosition({x: positionX, y: positionY}));

    };

    const onPanningStop = ({ positionX, positionY }) => {
        dispatch(setTechMapPosition({x: positionX, y: positionY}));
    };

    if(techMapData.readyStatus === REQUESTING)
        return <div className={classes.canvas}>
            <div className={classes.loader}>
                <CircularProgress />
            </div>
        </div>;

    return (
        <div className={classes.canvas}>
            <Backdrop open={open} className={classes.backdrop}/>
            <TransformWrapper
                scale={techMapScale}
                defaultScale={techMapScale}
                defaultPositionY={techMapPosition?.y ? techMapPosition?.y : 100}
                defaultPositionX={techMapPosition?.x ? techMapPosition?.x : 100}
                positionY={techMapPosition?.y}
                positionX={techMapPosition?.x}
                onWheelStop={onScaleStop}
                onPanningStop={onPanningStop}
                pan={{
                    wheelEnabled: useTouch
                }}
                wheel={{
                    step: 70,
                    wheelEnabled: useMouse,
                    touchPadEnabled: useTouch
                }}
                options={{
                    minScale: 0.3,
                    limitToBounds: false,
                    limitToWrapper: false
                }}
                zoomOut={{
                    step: 10
                }}
                zoomIn={{
                    step: 10
                }}
            >
                {({ zoomIn, zoomOut, resetTransform, ...rest }) => (
                    <React.Fragment>
                        <SpeedDialComponent
                            handleOpen={handleOpen}
                            handleClose={handleClose}
                            resetTransform={resetTransform}
                            zoomState={zoomState}
                            useTouch={useTouch}
                            useMouse={useMouse}
                            open={open}
                        />
                        <TransformComponent>
                            <div className={classes.techMapLayoutContainer}>
                                <TechMapLayout
                                    zoomState={zoomState}
                                    techMapData={techMapData}
                                    focusedCategory={focusedCategory}
                                    transformOptions={{
                                        positionX: rest.positionX,
                                        positionY: rest.positionY
                                    }}
                                />
                            </div>
                        </TransformComponent>
                    </React.Fragment>
                )}
            </TransformWrapper>
        </div>
    )
}