export default ({ scrollId , featuresItems}) => {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);
    const [expandedMoreButton, setMoreButtonExpanded] = useState(false);

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };
    
    return (
        <SectionLayout icon={<Tune />} title="Features" scrollId={scrollId}>
            <Typography variant="body1" component="p" className={classes.blockDescription}>
                Asi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse
                cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat lorem ipsim dolore solitas neno
                catortupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
            </Typography>
            {
                featuresItems?.slice(0, 4).map( (item, index) => {
                    return (
                        <Accordion expanded={expanded === item.id} onChange={handleChange(item.id)} key={item.id}>
                            <AccordionSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls={`${item.id}-content`}
                                id={`${item.id}-header`}
                            >
                                <Typography variant="h6" component="h6">{item['Feature Category']}</Typography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <div className={classes.contentItem} key={index}>
                                    <Typography variant="h6" component="h6" className={classes.itemSubtitle}>{item['Feature Name']}</Typography>
                                    <Typography variant="body1" component="p">{item.Description}</Typography>
                                </div>
                            </AccordionDetails>
                        </Accordion>
                    )
                })
            }

            <Collapse in={expandedMoreButton} timeout="auto" unmountOnExit style={{ marginTop: '1rem'}}>
            {
            featuresItems?.slice(4).map( (item, index) => {
                return (
                    <Accordion expanded={expanded === item.id} onChange={handleChange(item.id)} key={item.id}>
                        <AccordionSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls={`${item.id}-content`}
                            id={`${item.id}-header`}
                        >
                            <Typography variant="h6" component="h6">{item['Feature Category']}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <div className={classes.contentItem} key={index}>
                                <Typography variant="h6" component="h6" className={classes.itemSubtitle}>{item['Feature Name']}</Typography>
                                <Typography variant="body1" component="p">{item.Description}</Typography>
                            </div>
                        </AccordionDetails>
                    </Accordion>
                )
            })
            }
            </Collapse>
            <Button
                onClick={() => setMoreButtonExpanded(!expandedMoreButton)}
                endIcon={expandedMoreButton ? <Remove/> : <Add />}
                className={classes.moreButton}
            >
                {expandedMoreButton ? "Show less" : "Show more"}
            </Button>
        </SectionLayout>
    )
}