import { TOGGLE_EXPANDED_CAPABILITY_HEADER } from '../types';
import { produce } from "immer";

const initialState = {
    capabilityHeader: {
        expanded: false
    }
};

export default produce((state = initialState, action) => {
    switch (action.type) {
        case TOGGLE_EXPANDED_CAPABILITY_HEADER:
            state.capabilityHeader.expanded = action?.payload?.expanded;
            break;
        default:
            return state;
    }
})