import React from "react";
import {Box} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles( theme => ({
    container: {
        margin: `0 -8px ${theme.spacing(2)}`,
        [theme.breakpoints.down('xs')]: {
            flexWrap: 'wrap'
        }
    },
    item: {
        fontWeight: 'bold',
        cursor: 'pointer',
        margin: '4px 8px',
    },
    tagItem: {
        fontSize: theme.spacing(3),
        background: `${theme.palette.primary.main}1a`,
        color: theme.palette.primary.main,
        borderRadius: 4,
        padding: '4px 8px',
        transition: 'background .333s ease',
        '&:hover': {
            background: theme.palette.primary.main,
            color: theme.palette.white.main
        }
    },
    categoryItem: {
        color: theme.palette.primary.main,
        transition: 'color .333s ease',
        '&:hover': {
            color: theme.palette.darkBlue.main
        }
    }
}));

export default ({ tags, onClickHandler }) => {
    const classes = useStyles();

    const clickHandler = onClickHandler ? onClickHandler : () => {};
    return (
        <Box display="flex" flexDirection="row" alignItems="center" className={classes.container}>
            {tags.length > 1 ? <div onClick={() => clickHandler("")} className={`${ classes.item} ${classes.tagItem}`}>{"All"}</div> : ""}
            {
                tags?.map( (item, index) =>
                        <div key={index} onClick={() => clickHandler(item.name)} className={`${ classes.item} ${item.type === "tag" ? classes.tagItem : classes.categoryItem}`}>{item.name}</div>
                )
            }
        </Box>
    )
}