import React from "react";
import {Timeline} from "@material-ui/lab";
import {makeStyles} from "@material-ui/core/styles";
import TimelineItem from "@material-ui/lab/TimelineItem/TimelineItem";
import TimelineContent from "@material-ui/lab/TimelineContent/TimelineContent";

import Link from './ScrollLink';

const useStyles = makeStyles(theme => ({
    timelineContainer: {
        display: 'flex',
        alignItems: 'center'
    },
    timelineWrapper: {
        margin: 0,
        padding: 0,
        [theme.breakpoints.down('md')]: {
            fontSize: theme.spacing(3)
        },
        '& .active': {
            color: theme.palette.primary.main,
            fontWeight: 'bold',
            [theme.breakpoints.down('md')]: {
                fontSize: theme.spacing(4)
            }
        },
        '& .MuiTimelineItem-missingOppositeContent:before': {
            content: 'none'
        }
    },
    timelineItem: {
        minHeight: 'auto'
    },
    navItem: {
        minHeight: 30,
        display: 'flex',
        alignItems: 'center',
        cursor: 'pointer'
    },
    progressBarContainer: {
        height: 348,
        background: `${theme.palette.darkBlue.main}33`,
        width: 4,
        flex: '0 0 auto',
        borderBottomRightRadius: 1,
        borderBottomLeftRadius: 1,
        [theme.breakpoints.down('md')]: {
            height: 332
        },
    },
    progressBar: props => ({
        width: '100%',
        background: theme.palette.primary.main,
        transition: 'height .333s ease',
        height: `${props.step * (100/8)}%`,
        borderBottomRightRadius: 1,
        borderBottomLeftRadius: 1
    })
}));


const renderSidebarNav = ( setActiveStep, classes, navItems ) => {
    return navItems?.map((item, index) => {
        return <TimelineItem className={classes.timelineItem} key={index}>
            <TimelineContent>
                <Link
                    onSetActive={prop => {setActiveStep(prop)}}
                    activeClass={"active"}
                    className={classes.navItem}
                    to={`${index + 1}`}
                    data-step={`${index + 1}`}
                    spy={true}
                    smooth={true}
                    offset={-100}
                    isDynamic={true}
                    ignoreCancelEvents={false}
                >
                    {item}
                </Link>
            </TimelineContent>
        </TimelineItem>
    })
};

export default ({ step, setActiveStep, navItems }) => {
    const classes = useStyles({ step });

    return (
        <div className={classes.timelineContainer}>
            <div className={classes.progressBarContainer}>
                <div className={classes.progressBar} />
            </div>
            <Timeline className={classes.timelineWrapper}>
                {renderSidebarNav(setActiveStep, classes, navItems)}
            </Timeline>
        </div>
    )
}