import React, {useState} from "react";
import {School, Remove, Add} from "@material-ui/icons";

import SectionLayout from "../../SectionLayout";
import Typography from "@material-ui/core/Typography";
import {makeStyles} from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Collapse from "@material-ui/core/Collapse/Collapse";

const useStyles = makeStyles( theme => ({
    item: {
        marginTop: theme.spacing(4)
    },
    moreButton: {
        color: theme.palette.deepBlue.main,
        fontWeight: 300,
        marginLeft: 15,
        marginTop: theme.spacing(4)
    }
}));

export default ({ scrollId, termItems }) => {
    const classes = useStyles();
    const [expanded, setExpanded] = useState(false);

    return (
        <SectionLayout icon={<School />} title={"Terminology"} scrollId={scrollId}>
                {
                    termItems?.slice(0, 2).map(term => {
                        return (
                            <div key={term.id} className={classes.item}>
                                <Typography  variant="h6" component="h6">
                                   {term.Term} | {term["Term Acronym"]}
                                </Typography>
                                <Typography variant="body1" component="p">
                                    {term.Description}
                                </Typography>
                            </div>
                        );
                    })
                }
            <Collapse in={expanded} timeout="auto" unmountOnExit>
                {
                    termItems?.slice(2).map(term => {
                        return (
                            <div key={term.id} className={classes.item}>
                                <Typography  variant="h6" component="h6">
                                   {term.Term} | {term["Term Acronym"]}
                                </Typography>
                                <Typography variant="body1" component="p">
                                    {term.Description}
                                </Typography>
                            </div>
                        );
                    })
                }
            </Collapse>
            <Button
                onClick={() => setExpanded(!expanded)}
                endIcon={expanded ? <Remove/> : <Add />}
                className={classes.moreButton}
            >
                {expanded ? "Show less" : "Show more"}
            </Button>
        </SectionLayout>
    )
}