import React, { useEffect } from "react";
import {shallowEqual, useDispatch, useSelector} from "react-redux";

import { isMobile } from "../../utils";
import { TechMap as TechMapComponent } from '../../components';
import {fetchTechMapFunctionalCategoryIfNeeded, techMapZoomOutAction} from "../../store/actions/techMapActions";

export default () => {
    const dispatch = useDispatch();
    const { techMapData, techMapOptions } = useSelector(
        state => state.techMapReducer,
        shallowEqual
    );

    useEffect(() => {
        dispatch(fetchTechMapFunctionalCategoryIfNeeded());
        if(isMobile())
            dispatch(techMapZoomOutAction(3))
    }, []);

    return (
        <TechMapComponent
            techMapScale={techMapOptions?.scale}
            zoomState={techMapOptions?.zoomState}
            techMapData={techMapData}
            useTouch={techMapOptions?.useTouch}
            useMouse={techMapOptions?.useMouse}
            techMapPosition={techMapOptions?.position}
            focusedCategory={techMapOptions?.focusedCategory}
        />
    )
}