import React from "react";
import ReactPlayer from 'react-player/lazy'
import {withStyles} from "@material-ui/core";
import Dialog from '@material-ui/core/Dialog';
import CloseIcon from '@material-ui/icons/Close';
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from "@material-ui/core/DialogContent";
import {theme} from "../../theme";

const styles = (theme) => ({
    headerRoot: {
        margin: 0,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        [theme.breakpoints.up('md')]: {
            paddingTop: theme.spacing(10),
            paddingLeft: theme.spacing(12),
            paddingRight: theme.spacing(12)
        }
    },
    title: {
        position: 'relative',
        '&:before': {
            content: "''",
            display: 'block',
            width: 40,
            height: 4,
            borderRadius: 1,
            backgroundColor: theme.palette.deepBlue.main,
            marginBottom: 9
        }
    },
    closeButton: {
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.darkBlue.main,
    },
    playerWrapper: {
        height: '100%',
        [theme.breakpoints.down('sm')]: {
            position: 'relative',
            paddingTop: '56.25%',
            height: 'auto'
        }
    },
    reactPlayer: {
        [theme.breakpoints.down('sm')]: {
            position: 'absolute',
            top: 0,
            left: 0
        }
    }
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.headerRoot} {...other}>
            <Typography variant="h5" component="h5" className={classes.title}>{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

export default withStyles(styles)(({ open, handleClose, title, classes, link }) => {
    return (
        <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open} maxWidth="xl">
            <DialogTitle id="customized-dialog-title" onClose={handleClose}>
                {title}
            </DialogTitle>
            <DialogContent>
                <div className={classes.playerWrapper}>
                    <ReactPlayer
                        className={classes.reactPlayer}
                        youtube={{
                            playerVars: { rel: 0 }
                        }}
                        controls={true}
                        width='100%'
                        height='100%'
                        url={link}
                    />
                </div>
            </DialogContent>
        </Dialog>
    )
})