import React from "react";
import {useDispatch} from "react-redux";
import Box from "@material-ui/core/Box";
import {makeStyles} from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";

import {
    setTechMapFocusedCategory,
    setTechMapPosition,
    techMapZoomOutAction
} from "../../../store/actions/techMapActions";
import FunctionalHeaderBG_2 from "../../../images/functionalCategoryHeaderImage.png";

const useStyles = makeStyles( theme => ({
    tech: props => ({
        padding: props.zoomState === 3 && !props.isCategoryOpened ? `${theme.spacing(12)} ${theme.spacing(5)} ${theme.spacing(12)}` : `${theme.spacing(10)} ${theme.spacing(5)} ${theme.spacing(14)}`,
        textAlign: 'center',
        backgroundColor: props?.categoryOptions.color,
        borderRadius: !props.isCategoryOpened ?  `8px 8px 0 0` : 8,
        overflow: 'hidden',
        position: 'relative',
        zIndex: 3
    }),
    focusIcon: {
        position: 'absolute',
        top: 0,
        right: 0,
        color: theme.palette.white.main,
        zIndex: 10,
        '& > *': {
            fontSize: '2rem'
        }
    },
    techBGContainer: {
        width: '100%',
        height: 120,
        position: 'absolute',
        left: 0,
        top: 0,
        right: 0,
        zIndex: -1
    },
    techBGImage: {
        width: '100%',
        height: '100%'
    },
    defaultCategoryIcon: {
        color: theme.palette.white.main,
        marginRight: 15
    },
    headerWrapper: {

    },
    categoryIcon: {
        display: 'block',
        boxSizing: 'border-box',
        width: '100%',
        maxWidth: 90,
        [theme.breakpoints.down('sm')]: {
            maxWidth: 90
        }
    },
    techTitle: {
        color: theme.palette.white.main,
        whiteSpace: 'nowrap',
        cursor: 'pointer'
    },
    iconWrapper: {
        scale: 1,
        width: 90,
        height: 'calc(46.8px * 2)',
        marginRight: 15,
        '& > img': {
            scale: 1,
            width: '100%'
        }
    }
}));

export default ({ activeTech, functionalCategory, categoryOptions, zoomState, fitFocusedCategory }) => {
    /* Check if current category is opened */
    const isCategoryOpened = activeTech ? activeTech !== functionalCategory?.id : zoomState === 3;
    const classes = useStyles({ isCategoryOpened , functionalCategory, categoryOptions, zoomState });
    const dispatch = useDispatch();

    const categoryIcon = categoryOptions?.icon;

    const techFocusOnClick = value => {
        dispatch(setTechMapFocusedCategory(value));
    };

    const onFocusClick = e => {
        if(functionalCategory?.id === activeTech) {
            techFocusOnClick(null);
            dispatch(setTechMapFocusedCategory(null));
        }
        else {
            fitFocusedCategory(e.currentTarget.closest('.grid-item'));
            techFocusOnClick(functionalCategory?.id);
            dispatch(setTechMapPosition({ x: 100, y: 100 }));
            dispatch(techMapZoomOutAction(1));
        }
    };

    return (
        <div className={classes.tech}>
            <Box
                display="flex"
                alignItems="center"
                justifyContent="center"
                className={classes.headerWrapper}
            >
                <div className={classes.iconWrapper}>
                    <img src={categoryIcon} alt="Category Icon" className={classes.categoryIcon} />
                </div>
                <Typography className={classes.techTitle} variant="h2" component="h2" onClick={e => onFocusClick(e)}>
                    {functionalCategory?.name}
                </Typography>
            </Box>
            <div className={classes.techBGContainer}>
                <img src={FunctionalHeaderBG_2} alt="waves" className={classes.techBGImage} />
            </div>
        </div>
    )
}