import React, {useState} from 'react';
import Button from "@material-ui/core/Button";
import MoreVert from "@material-ui/icons/MoreVert";
import Close from "@material-ui/icons/Close";
import {makeStyles} from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";

import SidebarMenu from '../SidebarMenu';
import Box from "@material-ui/core/Box";
import Paper from "@material-ui/core/Paper";
import Hidden from "@material-ui/core/Hidden";

const useStyles = makeStyles( theme => ({
    mobileSidebarNavigationContainer: {
        position: 'sticky',
        top: 100,
        margin: theme.spacing(4),
        zIndex: 100,
        [theme.breakpoints.down('sm')]: {
            top: 86,
            margin: theme.spacing(2)
        }
    },
    toggleButton: {
        background: theme.palette.white.main,
        color: theme.palette.primary.main,
        '& .MuiButton-startIcon': {
            color: theme.palette.darkBlue.main
        },
        '&:hover': {
            background: theme.palette.primary.main,
            color: theme.palette.white.main
        },
        [theme.breakpoints.down('sm')]: {
            borderRadius: theme.shape.borderRadius,
            color: theme.palette.darkBlue.main,
            boxShadow: '0px 5px 24px rgba(22, 46, 70, 0.24)'
        }
    },
    dropdownMenu: props => ({
        background: theme.palette.white.main,
        position: 'absolute',
        top: 0,
        left: 0,
        transition: 'transform .333s ease',
        width: 224,
        transform: `translateX(${props.isOpen ? '0' : '-120%'})`,
        willChange: 'transform',
        padding: theme.spacing(4),
        boxShadow: '0px 5px 30px rgba(22, 46, 70, 0.15)'
    }),
    closeButton: {
        position: 'absolute',
        top: 10,
        right: 10,
        color: theme.palette.darkBlue.main
    }
}));

export default ({ step, setActiveStep, navItems }) => {
    const [isOpen, toggle] = useState(false);
    const classes = useStyles({ isOpen });
    return (
        <div className={classes.mobileSidebarNavigationContainer}>
            <Hidden xsDown>
                <Button
                    variant="contained"
                    startIcon={<MoreVert />}
                    className={classes.toggleButton}
                    onClick={() => toggle(!isOpen)}
                >
                    {navItems[step - 1]}
                </Button>
            </Hidden>
            <Hidden smUp>
                <IconButton onClick={() => toggle(!isOpen)} className={classes.toggleButton}>
                    <MoreVert />
                </IconButton>
            </Hidden>

            <Paper className={classes.dropdownMenu}>
                <SidebarMenu step={step} setActiveStep={setActiveStep} navItems={navItems} />

                <IconButton onClick={() => toggle(false)} className={classes.closeButton}>
                    <Close />
                </IconButton>
            </Paper>
        </div>
    )
}