const renderDateSortIcon = (classes) => {
    return (
        <span className={classes.sortIcon}>
            <span className={classes.dateArrowTop} />
            <span className={classes.dateArrowBottom} />
        </span>
    )
};

const renderAlphabetSortIcon = (classes) => {
    return (
        <span className={classes.sortIcon}>
            <span className={classes.alphabetArrowTop} />
            <span className={classes.alphabetArrowBottom} />
        </span>
    )
};

const renderSortBy = (sortDate, sortAlphabet, onDateSort, onAlphabetSort, classes) => {
    return (
        <Box display="flex" alignItems="center" className={classes.sortItems}>
            Sort by
            <span className={classes.sortItem} onClick={() => onDateSort()}>Publish {renderDateSortIcon(classes)}</span>
            <span className={classes.sortItem} onClick={() => onAlphabetSort()}>Date A-Z {renderAlphabetSortIcon(classes)}</span>
        </Box>
    )
};

export default ({ scrollId, resourcesItems}) => {
    const [sortDate, setSortDate] = useState('asc');
    const [sortAlphabet, setSortAlphabet] = useState(null);
    const classes = useStyles({sortDate, sortAlphabet});

    const onDateSort = () => {
        if(sortDate === 'asc') {
            setSortDate('desc');
        }
        else {
            setSortDate('asc');
        }
        setSortAlphabet(false);
    };

    const onAlphabetSort = () => {
        if(sortAlphabet === 'asc') {
            setSortAlphabet('desc');
        }
        else {
            setSortAlphabet('asc');
        }
        setSortDate(false)
    };

    return (
        <SectionLayout
            icon={<Public />}
            title="Links & material"
            scrollId={scrollId}
            headerRightSideContent={renderSortBy(sortDate, sortAlphabet, onDateSort, onAlphabetSort, classes)}
        >
            <div className={classes.linksWrapper}>
                {
                    resourcesItems?.map((item) => {
                        return (
                            <div className={classes.linkItem} key={item.id}>
                                <Typography varint="body1">
                                    <Link href={item['Resource Link']} className={classes.link} component="a">
                                        {item['Resource Name']}
                                    </Link>
                                </Typography>
                                <div className={classes.linkDescription}>
                                    <Typography varint="body1" component="p">{item['Resource Publish Date']}</Typography>
                                    <div className="divider" />
                                    <Typography varint="body1" component="p">{item['Resource Type']}</Typography>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
            <Pagination count={10} variant="outlined" shape="rounded" className={classes.pagination}/>
        </SectionLayout>
    )
}