import React, {useEffect, useRef, useState} from 'react';
import Packery from 'packery';
import {makeStyles} from "@material-ui/core/styles";

import FunctionalCategory from './FunctionalCategory';

const useStyles = makeStyles(() => ({
    groups: {
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'nowrap'
    },
    masonryGrid: {
        display: 'flex',
        width: '3000px',
        maxWidth: '100%'
    },
    column: {
        width: "auto !important"
    },
    packeryContainer: props => ({
        width: props.focusedCategory ? 2800 : 5000
    })
}));

export default ({ zoomState, techMapData, transformOptions, focusedCategory }) => {
    const [packery, setPackery] = useState(null);
    const packeryContainer = useRef(null);
    const classes = useStyles({ focusedCategory });
    
    const handleResize = () => {
        packeryRerender();
    };

    useEffect(() => {
        if(packery) {
            window.addEventListener('resize', handleResize);
            const focusedItem = document.querySelector(`[data-key*="${focusedCategory}"]`);
            if(focusedItem) {
                fitFocusedCategory(focusedItem)
            }
        }
    }, [packery]);

    useEffect(() => {
        initializePackery();
    }, []);
    
    useEffect(() => {
        packeryRerender()
    }, [zoomState]);

    const initializePackery = () => {
        const packeryInst = new Packery(packeryContainer.current, {
            itemSelector: '.grid-item',
            gutter: 30
        });
        setPackery(packeryInst);
    };

    useEffect(() => {
        packeryRerender();
    }, [focusedCategory]);

    const packeryRerender = () => {
        if(packery) {
            packery.layout();
        }
    };

    const fitFocusedCategory = e => {
        if(packery) {
            packery.fit(e, 0 , 0);
            packeryRerender();
        }
    };
    return (
        <div className={classes.packeryContainer} ref={packeryContainer}>
            {
                techMapData?.data?.map((functionalCategory) => {
                    return (
                        <div className="grid-item" key={functionalCategory?.id} data-key={functionalCategory?.id}>
                            <FunctionalCategory
                                zoomState={zoomState}
                                activeTech={focusedCategory}
                                transformOptions={transformOptions}
                                fitFocusedCategory={fitFocusedCategory}
                                functionalCategory={functionalCategory}
                            />
                        </div>
                    )
                })
            }
        </div>
    )
}
