import React from "react";
import { useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'

import Header from './Header';
import CapabilityContent from './CapabilityContent';
import Contribute from './CapabilityContent/Content/Contribute';
import { fetchSingleCapability } from "../../store/actions/techMapActions";

export default () => {
    const capability = useSelector(state => state.techMapReducer.singleCapability);
    const id = new URLSearchParams(window.location.search).get('id');
    const dispatch = useDispatch();
    useEffect(() => {
        if(!capability) {
            dispatch(fetchSingleCapability(id));
        }
    }, []);
    const breadcrumbs = `${capability?.relatedFunctionalCategory?.['Function Short Name']} | ${capability?.relatedCapabilityCategory?.['Category Name']}`;
    const title = capability?.capabilityName;
    const acronym = capability?.capabilityAcronym;
    const description = capability?.description;
    const keyBenefits = capability?.relatedKeyBenefits;
    const keyConsiderations = capability?.relatedKeyConsiderations;
    const tehchnologyCategory = capability?.relatedTehchnologyCategory;
    const features = capability?.relatedFeatures
    const terms = capability?.relatedTerms;
    const resources = capability?.relatedResources;
    const contributors = capability?.relatedContributors;
    const capabilities = capability?.relatedCapabilities;
    console.log(capability)
    return (
        <>
            <Header 
                breadcrumbs={breadcrumbs} 
                title={title} 
                acronym={acronym}  
            />
            <CapabilityContent 
                description={description}
                KeyBenefitsItems={keyBenefits}
                keyConsiderationsItems={keyConsiderations}
                technologyCategoryItems={tehchnologyCategory}
                featuresItems={features}
                termItems={terms}
                resourcesItems={resources}
                contributorsItem={contributors}
                capabilitiesItems={capabilities}
            />
            <Contribute />
        </>
    )
}