import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import Link from "@material-ui/core/Link";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles(theme => ({
    sidebarComponent: {
        display: 'block'
    },
    header: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        marginBottom: theme.spacing(4)
    },
    linksWrapper: {
        display: 'flex',
        flexDirection: 'column'
    },
    linkItem: {
        margin: `${theme.spacing(1)} 0`,
        paddingLeft: 18,
        position: 'relative',
        color: theme.palette.deepBlue.main,
        '&:before': {
            content: "''",
            position: 'absolute',
            width: 8,
            height: 2,
            borderRadius: 1,
            background: theme.palette.darkBlue.main,
            left: 0,
            top: '50%',
            transform: 'translateY(-50%)'
        }
    }
}));

export default ({ capabilitiesItems }) => {
    const classes = useStyles();

    return (
        <div className={classes.sidebarComponent}>
            <div className={classes.header}>
                <Typography variant="h5" component="h5">Related capabilities</Typography>
            </div>
            <div className={classes.linksWrapper}>
            {capabilitiesItems?.map(item => {
                return (<Link href={`/capability?id=${item.id}`} key={item.id} className={classes.linkItem} component="a">
                            {item['Capability Name']}
                        </Link>)
            })
            }
            </div>
        </div>
    )
}