import React from 'react';

import { AboutPage, MainPage, CapabilityPage, SupportPage, PolicyPage, ContactUsPage, NotFound } from './pages';

const routes = [
    {
        path: "/",
        exact: true,
        component: MainPage
    },
    {
        path: "/about",
        exact: true,
        component: AboutPage
    },
    {
        path: "/support",
        component: SupportPage
    },
    {
        path: "/policy",
        component: PolicyPage
    },
    {
        path: "/contact-us",
        component: ContactUsPage
    },
    {
        path: '/404',
        exact: true,
        component: NotFound
    },
    {
        path: "/:capabilityId",
        component: CapabilityPage
    }
];

export default routes;